

$(document).ready(function () {
  // ,sideBar
  let pathName = location.pathname;          //
  let length = pathName.length;              //
  let name = pathName.substring(1, length - 5)  //
  let target = '#' + name;
  if (name == "/") {
    $('#index').addClass('active');
  } else {
    $(target).addClass('active');
  }
})


